// import styles from './AddPanorama.module.css';
import OffCanvasModal from './../off-canvas-modal/OffCanvasModal';
import Input from '../input/Input';
import { Col, Row } from 'react-bootstrap';
import AppButton from './../button/AppButton';
import { useState } from 'react';
import { useAppStore } from './../../hooks/store/useAppStore';
import { addNewPanoSelector } from './../../hooks/store/setters';

const panoFormInitialState = {
    id: Date.now(),
    name: '',
    image: '',
    pan: 0,
    tilt: 0,
    zoom: null,
    cameraFree: true,
    hotspots: []
}

const AddPanoramaModal = ({ show, handleClose }) => {
    
    const [panoForm, setPanoForm] = useState({ ...panoFormInitialState });
    
    const addNewPano = useAppStore(addNewPanoSelector);
    
    const handleInputChange = ({ target: { name, value } }) => {
        setPanoForm({
            ...panoForm,
            [name]: value
        })
    }

    const handleInputToggle = ({ target: { name, value } }) => {
        setPanoForm({
            ...panoForm,
            [name]: !panoForm[name]
        })
    }
    
    const handleFormSubmit = () => {
        console.log('add new pano ', panoForm);
        addNewPano(panoForm);
    }
    
    const ModalFooter = () => {
        return (
            <Row>
                <Col md={6}>
                    <AppButton
                        label="Save"
                        fullWidth
                        onClick={handleFormSubmit}
                    />
                </Col>
                <Col md={6}>
                    <AppButton variant="secondary" 
                        label="Close"
                        fullWidth
                        onClick={handleClose}
                    />
                </Col>
            </Row>
        )
    }
    
    if (!show) return null;
    
    return (
        <OffCanvasModal title="Add Panorama"
            show={show}
            onClose={handleClose}
            footer={<ModalFooter />}
        >
            <div className={StyleSheet.addPanoramaForm}>
                <Input 
                    label="Pano Name" 
                    type="text"
                    name="name"
                    value={panoForm.name}
                    onChange={handleInputChange}
                />
                <Input 
                    label="Pano Image Url" 
                    type="text"
                    name="image"
                    value={panoForm.image}
                    onChange={handleInputChange}
                />
                <Input 
                    label="Allow Look Around" 
                    type="checkbox"
                    name="cameraFree"
                    checked={!!panoForm.cameraFree}
                    onChange={handleInputToggle}
                />
                <Row>
                    <Col md={6}>
                        <Input 
                            label="Pan" 
                            type="number"
                            name="pan"
                            value={panoForm.pan}
                            onChange={handleInputChange}
                        />
                    </Col>
                    <Col md={6}>
                        <Input 
                            label="Tilt" 
                            type="number"
                            name="tilt"
                            value={panoForm.tilt}
                            onChange={handleInputChange}
                        />
                    </Col>
                </Row>
            </div>
        </OffCanvasModal>
    )
}

export default AddPanoramaModal;