import Input from '../input/Input';
import { useAppStore } from './../../hooks/store/useAppStore';
import { getCurrentPanoSelector, panosSelector } from './../../hooks/store/getters';
import { useEffect, useState } from 'react';
import { Card } from 'react-bootstrap';

const MoveHotspotForm = ({ handleInputChange, hotspot }) => {
    
    const panos = useAppStore(panosSelector);
    const currentPano = useAppStore(getCurrentPanoSelector);

    const [panosOptions, setPanoOptions] = useState([]);
    
    useEffect(() => {
        const options = panos.map(p => ({ value: p.id, label: p.name }))
                                .filter(o => o.value !== currentPano.id);
        setPanoOptions(options)

    }, [panos, currentPano])
    
    
    return (
        <>
            <Card>
                <Card.Header className="text-uppercase">Move Details</Card.Header>
                <Card.Body>
                    <Input 
                        label="Transition Video" 
                        type="text"
                        name="transitionVideo"
                        value={hotspot.transitionVideo}
                        onChange={handleInputChange}
                    />
                    <Input 
                        label="Go To Pano" 
                        type="select"
                        name="goToPano"
                        options={panosOptions}
                        value={hotspot.goToPano}
                        onChange={handleInputChange}
                    />
                </Card.Body>
            </Card>
        </>
    )
}

export default MoveHotspotForm;