import { Card, Col, Row } from "react-bootstrap";
import Input from "../input/Input";

const FORM_DATA_KEY = 'patchVideo3D';

const PatchVideo3DHotspotForm = ({ hotspotForm: hotspot, setHotspotForm }) => {

    const handleFormInputChange = ({ target: { name, value } }) => {

        const data = hotspot[FORM_DATA_KEY] || {};

        setHotspotForm(hotspotForm => ({
            ...hotspotForm,
            [FORM_DATA_KEY]: {
                ...data,
                [name]: value
            }
        }))
    }

    return (
        <>
            <Card>
                <Card.Header className="text-uppercase">3D Video Patch</Card.Header>
                <Card.Body>
                    <Input 
                        label="Video Source" 
                        type="text"
                        name="patchSource"
                        value={hotspot[FORM_DATA_KEY]?.patchSource}
                        onChange={handleFormInputChange}
                    />
                    <Row>
                        <Col md={6}>
                            <Input 
                                label="Patch Width" 
                                type="number"
                                name="patchWidth"
                                value={hotspot[FORM_DATA_KEY]?.patchWidth}
                                onChange={handleFormInputChange}
                            />
                        </Col>
                        <Col md={6}>
                            <Input 
                                label="Patch Height" 
                                type="number"
                                name="patchHeight"
                                value={hotspot[FORM_DATA_KEY]?.patchHeight}
                                onChange={handleFormInputChange}
                            />
                        </Col>
                    </Row>
                    
                </Card.Body>
            </Card>
        </>
    )
}

export default PatchVideo3DHotspotForm;