import styles from './EventConfiguration.module.css';
import OffCanvasModal from './../off-canvas-modal/OffCanvasModal';
import { Col, Row } from 'react-bootstrap';
import AppButton from './../button/AppButton';
import { useState } from 'react';
import { useAppStore } from './../../hooks/store/useAppStore';

const EventConfigurationModal = ({ show, handleClose }) => {
    
    const eventData = useAppStore(state => state.event);
    const [showExportData, setShowExportData] = useState(false);
    
    const handleFormSubmit = () => {};
    
    const copyToClipBoard = () => {
        const text = JSON.stringify(eventData, null, 4);
        navigator.clipboard.writeText(text);
        document.execCommand("copy");
        window.alert('Copied!');
    }
    
    const ModalFooter = () => {
        return (
            <Row>
                <Col md={6}>
                    <AppButton
                        label="Save"
                        fullWidth
                        onClick={handleFormSubmit}
                    />
                </Col>
                <Col md={6}>
                    <AppButton variant="secondary" 
                        label="Close"
                        fullWidth
                        onClick={handleClose}
                    />
                </Col>
            </Row>
        )
    }
    
    if (!show) return null;
    
    return (
        <OffCanvasModal title="Event Configuration"
            show={show}
            onClose={handleClose}
            footer={<ModalFooter />}
        >
            <div className={styles.eventConfigForm}>
                <div className={styles.eventExportDataHolder}>
                    <h6>Event Data</h6>
                    <Row>
                        <Col md={6}>
                            {
                               showExportData ?  <AppButton
                                    variant="outline-dark"
                                    label="Hide"
                                    fullWidth
                                    onClick={() => setShowExportData(false)}
                                /> : <AppButton
                                    variant="outline-dark"
                                    label="View"
                                    fullWidth
                                    onClick={() => setShowExportData(true)}
                                />
                            }
                            
                        </Col>
                        <Col md={6}>
                            <AppButton variant="outline-dark"
                                label="Copy"
                                fullWidth
                                onClick={copyToClipBoard}
                            />
                        </Col>
                    </Row>
                    {
                        showExportData && <pre>
                            { JSON.stringify(eventData, null, 4) }
                        </pre>
                    }
                </div>
            </div>
        </OffCanvasModal>
    )
}

export default EventConfigurationModal;