import styles from "./Panorama.module.css";
import React, { Suspense, useRef } from 'react';
import { Canvas } from '@react-three/fiber';
import { Loader, OrbitControls } from '@react-three/drei';
import Camera from './Camera';
import ImageSphere from './ImageSphere';
import { useAppStore } from './../../hooks/store/useAppStore';
import { MEDIA_BASE_URL } from "../../constants/app.constant";
import { getCurrentPanoSelector } from "../../hooks/store/getters";
import PanoHotspots from './PanoHotspots';
import { addHotspotInCurrentPanoSelector } from "../../hooks/store/setters";
import { HOTSPOT_INITIAL_STATE } from "../../constants/hotspot.constant";
import PatchVideo3D from "../../components/hotspots/PatchVideo3D";

const Panorama = ({ viewMode=false }) => {
    
    const currentPano = useAppStore(getCurrentPanoSelector);
    const addHotspotInCurrentPano = useAppStore(addHotspotInCurrentPanoSelector);

    const group = useRef(null);
    
    const addNewHotspot = ({x, y, z}) => {
        if (viewMode) return;
        
        console.log('add new hotspot ', {x,y,z});
        addHotspotInCurrentPano({
            ...HOTSPOT_INITIAL_STATE,
            id: Date.now(),
            position: { x, y, z }
        })
    };
    
    if (!currentPano) return null;
    
    const { image, pan, tilt, cameraFree, hotspots, id } = currentPano;
    // const { image, pan, tilt, cameraFree, zoom, id, hotspots } = currentPano;
    
    return (
        <>
            <Canvas className={styles.canvasWrapper}
                gl={{ alpha: false }} 
                colorManagement={false} 
            >
                <Suspense fallback={null}>
                    {/* <ambientLight /> */}

                    <Camera
                        position={[0, 0, -0.1]} 
                        pan={pan} 
                        tilt={tilt} 
                        panoId={id}
                        // defaultZoom={zoom} 
                    />
                    
                    <OrbitControls 
                        enableKeys={true} 
                        rotateSpeed={-1} 
                        enableZoom={!!cameraFree} 
                        enablePan={false} 
                        dampingFactor={0.1} 
                        enabled={!!cameraFree} 
                        minDistance={5} 
                        maxDistance={950} 
                    />
                    
                    
                    <group ref={group}>
                        <ImageSphere
                            url={MEDIA_BASE_URL + image} 
                            // url="https://s3-us-east-2.amazonaws.com/ve-dev/2bacb2e7-3b76-480f-b0d1-57af57756462.jpg" 
                            position={[0, 0, 0]} 
                            key={image} 
                            addNewHotspot={addNewHotspot}
                        />
                        <PanoHotspots hotspots={hotspots} viewMode={viewMode} />
                        {/* <HotspotModal /> */}
                    </group>
                    {/* <PatchVideo3D 
                        hotspot={{
                            "id": 1631014444476,
                            "name": "patch",
                            "type": "PATCHVIDEO3D",
                            "position": {
                                "x": -826.8792682917745,
                                "y": -486.51027138602586,
                                "z": 278.0386724248006
                            },
                            "patchVideo3D": {
                                "patchSource": "https://s3-us-east-2.amazonaws.com/ve-dev/a6a8a521-11a4-44dd-96cf-7308bb9d8a34.m4v",
                                "patchWidth": "916.4",
                                "patchHeight": "1083"
                            }
                        }}
                        viewMode={true}
                        index={10}
                    /> */}
                </Suspense>
            </Canvas>
            <Loader />
        </>
    );
};

export default Panorama;