const getPosition = (tilt, pan) => {
    var position = new THREE.Vector3(0, 0, 0);
    console.log(tilt, pan);
    if (pan < 0) {
        pan = 360 + pan;
    }
    position.setFromSphericalCoords(900,
        THREE.MathUtils.degToRad(90 - tilt),
        THREE.MathUtils.degToRad(- pan));

    console.log('get position :: ', position)

    return position;
};

