import styles from './Hotspot.module.css';
import { Html } from '@react-three/drei';
// import { BsExclamationTriangle } from "react-icons/bs"
import { setCurrentHotspotSelector, setShowHotspotConfigModalSelector } from './../../hooks/store/setters';
import { useAppStore } from './../../hooks/store/useAppStore';
import { MEDIA_BASE_URL } from '../../constants/app.constant';

const DEFAULT_ICON = 'media/images/hotspot/info.gif'


const DefaultHotspot = (props) => {

    const { index, hotspot, onClick=()=>{}, viewMode } = props;
    const { position: { x, y, z } } = hotspot;

    const setCurrentHotspot = useAppStore(setCurrentHotspotSelector);
    
    const setShowHotspotConfigModal = useAppStore(setShowHotspotConfigModalSelector);
    
    const handleHotspotClick = () => {
        console.log('handleHotspotClick ', { viewMode });

        if (viewMode) {
            onClick();
        } else {
            editHotspot();
        }
    }

    const editHotspot = () => {
        console.log('editHotspot ', { hotspot, index })
        setCurrentHotspot({ hotspot, index })
        setShowHotspotConfigModal(true)
    }

    
    return (
        <Html 
            {...props} 
            scaleFactor={1} 
            position={[x, y, z]} 
            center 
            zIndexRange={[9, 1]}
            className={styles.hotspot}
        >
            { hotspot.name && 
                <div className={styles.hotspotTextContainer} onClick={handleHotspotClick}>
                    <span className={styles.hotspotDisplayName}> {hotspot.name} </span>
                </div>
            }
            <div className={styles.hotspotImg}>
                <img 
                    src={MEDIA_BASE_URL + (hotspot.hotspotIcon ? hotspot.hotspotIcon : DEFAULT_ICON)}
                    alt="hotspot" 
                    onClick={handleHotspotClick} 
                />
            </div>
        </Html>
    );
}

export default DefaultHotspot;