import styles from "./AddPanorama.module.css";
import { MdAdd } from "react-icons/md";
import Button from 'react-bootstrap/Button';
import AddPanoramaModal from "./AddPanoramaModal";
import { useState } from "react";

const AddPanoramaButton = () => {
    
    const [show, setShow] = useState(false);
    
    const hideModal = () => setShow(false);
    
    return (
        <>
            <Button 
                className={styles.AddPanoBtn}
                onClick={() => setShow(true)}
            >
                <MdAdd />
            </Button>
            
            { show && <AddPanoramaModal show={show} handleClose={hideModal} /> }
        </>
    )
}

export default AddPanoramaButton;