import { HOTSPOT_TYPES } from '../../constants/hotspot.constant';
import DefaultHotspot from './DefaultHotspot';
import MoveHotspot from './MoveHotspot';
import IframeHotspot from './IframeHotspot';
import UrlHotspot from './UrlHotspot';
import PatchVideo3D from './PatchVideo3D';

const Hotspot = (props) => {
    
    const { hotspot } = props;

    switch (hotspot.type) {
        case HOTSPOT_TYPES.MOVE:
            return <MoveHotspot {...props} />
        
        case HOTSPOT_TYPES.IFRAME:
            return <IframeHotspot {...props} />
        
        case HOTSPOT_TYPES.URL:
            return <UrlHotspot {...props} />
        
        case HOTSPOT_TYPES.PATCHVIDEO3D:
            return <PatchVideo3D {...props} />
        
        default:
            return <DefaultHotspot {...props} />
    }
    
};

export default Hotspot;