import styles from "./EditPanorama.module.css";
import { MdEdit } from "react-icons/md";
import Button from 'react-bootstrap/Button';
import EditPanoramaModal from "./EditPanoramaModal";
import { useState } from "react";

const EditPanoramaButton = () => {
    
    const [show, setShow] = useState(false);
    
    const hideModal = () => setShow(false);
    
    return (
        <>
            <Button 
                variant="warning"
                className={styles.EditPanoBtn}
                onClick={() => setShow(true)}
            >
                <MdEdit />
            </Button>
            
            { show && <EditPanoramaModal show={show} handleClose={hideModal} /> }
        </>
    )
}

export default EditPanoramaButton;