import { useRef, useEffect, useState } from 'react';
import { PerspectiveCamera } from '@react-three/drei';
import * as THREE from 'three';
import { PANO_SPHERE_SIZE } from './../../constants/app.constant';

const Camera = (props) => {
    
    const { pan = 0, tilt = 0, defaultZoom = 1, panoId } = props;
    
    const cameraRef = useRef();
    const [position, setPosition] = useState([0, 0, 0]);
    
    const getPosition = (tilt, pan) => {
        let parsedPan = parseFloat(pan);
        let parsedTilt = parseFloat(tilt);
        
        let position = new THREE.Vector3(0, 0, 0);
        if (parsedPan < 0) {
            parsedPan = 360 + parsedPan;
        }
        position.setFromSphericalCoords(PANO_SPHERE_SIZE,
            THREE.MathUtils.degToRad(90 - parsedTilt),
            THREE.MathUtils.degToRad(- parsedPan)
        );

        return position;
    };
    
    useEffect(() => {
        if (!cameraRef.current) return;

        const { x, y, z } = getPosition(tilt, pan);
        setPosition([ x, y, z ]);

        cameraRef.current.position.set(x, y, z).normalize().negate();
        cameraRef.current.updateProjectionMatrix();
        console.log('update camera ', {pan, tilt});

    }, [pan, tilt, defaultZoom, panoId]);
    
    return (
        <PerspectiveCamera 
            makeDefault 
            {...props} 
            fov={61} 
            ref={cameraRef} 
            // position={position}
            // zoom={defaultZoom} 
        />
    )
};

export default Camera;