import { Modal } from "react-bootstrap";

const PopupModal = ({ 
    show,
    onHide,
    size="lg",
    fullscreen=false,
    title="",
    children=null,
}) => {
    
    return (
        <Modal show={show} onHide={onHide} size={size} fullscreen={fullscreen} centered>
            <Modal.Header closeButton>
                <Modal.Title>{ title }</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                { children }
            </Modal.Body>
            {/* <Modal.Footer>
                <Button variant="secondary" onClick={handleModalClose}>
                    Close
                </Button>
                <Button variant="primary" onClick={handleModalClose}>
                    Save Changes
                </Button>
            </Modal.Footer> */}
        </Modal>
    )
}

export default PopupModal;