import { Button } from "react-bootstrap"
import styles from './LoadingScreen.module.css';
import ProgressBar from 'react-bootstrap/ProgressBar';

const LoadingScreen = ({ show, startYourExperience, percentage=0 }) => {
    
    if (!show) return null;

    return (
        <div className={styles.loadingScreenWrapper}>
            <div>

                {
                    percentage < 100 
                    ? <>
                        <h6>Loading... {percentage}% </h6>
                        <ProgressBar animated now={percentage} />
                    </>
                    : <Button
                        size="lg"
                        variant="warning"
                        onClick={startYourExperience}
                        className="text-uppercase"
                    >Start Your Experience</Button>
                }

            </div>
        </div>
    )
}

export default LoadingScreen;