import { Button, Card } from "react-bootstrap";
import Input from "../input/Input";

const FORM_DATA_KEY = 'iframeDetails';

const IframeHotspotForm = ({ hotspotForm: hotspot, setHotspotForm }) => {

    const handleFormInputChange = (e, formIdx) => {
        const { name, value } = e.target;

        setHotspotForm(hotspotForm => ({
            ...hotspotForm,
            [FORM_DATA_KEY]: [
                ...hotspotForm[FORM_DATA_KEY].map((data, idx) => {
                    if (idx === formIdx) {
                        return { 
                            ...data,  
                            [name]: value
                        }
                    }
                    return data
                }),
            ]
        }))
    }

    const addNewForm = () => {
        const newForm = {
            title: '',
            url: '',
        }

        const data = hotspot[FORM_DATA_KEY] || []

        setHotspotForm(hotspotForm => ({
            ...hotspotForm,
            [FORM_DATA_KEY]: [
                ...data,
                newForm
            ]
        }))
    }

    const deleteForm = formIdx => {
        setHotspotForm(hotspotForm => ({
            ...hotspotForm,
            [FORM_DATA_KEY]: [
                ...hotspotForm[FORM_DATA_KEY].filter((f, fIdx) => fIdx !== formIdx)
            ]
        }))
    }

    return (
        <>
            <Card>
                <Card.Header className="text-uppercase">Iframe Details</Card.Header>
                <Card.Body>
                    <div>
                        {
                            hotspot[FORM_DATA_KEY]?.map((form, formIdx) => (
                                <Card body bg="light" className="mb-2" key={formIdx} >
                                    <Input 
                                        label="Title" 
                                        type="text"
                                        name="title"
                                        value={form.title}
                                        onChange={(e) => handleFormInputChange(e, formIdx)}
                                    />
                                    <Input 
                                        label="Link" 
                                        type="text"
                                        name="url"
                                        value={form.url}
                                        onChange={(e) => handleFormInputChange(e, formIdx)}
                                    />
                                    <Button className="ms-1" 
                                        variant="outline-danger" 
                                        size="sm"
                                        onClick={() => deleteForm(formIdx)}
                                    >Delete</Button>
                                </Card>
                            ))
                        }
                    </div>
                    <div className="mt-3">
                        <Button className="ms-1" 
                            variant="dark" 
                            onClick={addNewForm}
                        >+ Add</Button>
                    </div>
                </Card.Body>
            </Card>
        </>
    )
}

export default IframeHotspotForm;