import Panorama from './../panorama/Panorama';
import Header from './../../components/header/Header';
import WelcomeVideo from '../../components/welcome-video/WelcomeVideo';
import { useEffect, useState } from 'react';
import LoadingScreen from '../../components/loading-screen/LoadingScreen';
import PreloadPanos from '../../components/preload/PreloadPanos';
import { Suspense } from 'react';
import { useProgress } from '@react-three/drei';


const Event = () => {

    const [showLoader, setShowLoader] = useState(true);
    const [showWelcomeVideo, setShowWelcomeVideo] = useState(false);
    const [showPanorama, setShowPanorama] = useState(false);

    const [loadPercentValue, setLoadPercentValue] = useState(0);

    const onWelcomeVideoEnded = () => {
        console.log('welcome video ended. Starting panorama');
        setShowPanorama(true);
        setShowWelcomeVideo(false);
    }

    const startYourExperience = () => {
        setShowWelcomeVideo(true);
        setShowLoader(false);
    }

    const Loader = () => {
        console.log('Loader called');
        const { active, progress, errors, item, loaded, total } = useProgress()
        // const progress = useProgress();
        
        useEffect(() => {
            console.log('loader useProgress ', progress);
            setLoadPercentValue(progress)
        }, [progress])

        return null;
    }
    
    

    return (
        <>
            {
                showLoader && <LoadingScreen 
                    show={showLoader} 
                    startYourExperience={startYourExperience}
                    percentage={loadPercentValue}
                />
            }

            <Header />

            <Suspense fallback={<Loader />}>
                <PreloadPanos />
            </Suspense>

            { showPanorama && <Panorama viewMode={true} /> }

            { showWelcomeVideo && <WelcomeVideo onVideoEnded={onWelcomeVideoEnded} /> }
        </>
    )
}

export default Event;