import styles from './Hotspot.module.css';
import DefaultHotspot from './DefaultHotspot';
import { useState } from 'react';
import { Html } from "@react-three/drei";
import PopupModal from '../popup-modal/PopupModal';
import ListGroup from 'react-bootstrap/ListGroup';
import { Button } from 'react-bootstrap';

const IframeHotspot = ({ hotspot, index, viewMode }) => {
    
    const [showModal, setShowModal] = useState(false);
    const [showItemModal, setShowItemModal] = useState(false);
    const [item, setItem] = useState(null);
    
    const handleModalClose = () => {
        setShowModal(false);
    }
    
    const handleClick = () => {
        setShowModal(true);
    }

    const viewItem = (item) => {
        setItem(item);
        setShowItemModal(true);
    }

    const handleItemModalClose = () => {
        setShowItemModal(false);
        setItem(null);
    }

    const handleIframeLoaded = () => {
        console.log('iframe loaded');
    }
    
    return (
        <>
            <DefaultHotspot
                hotspot={hotspot} 
                viewMode={viewMode} 
                index={index} 
                onClick={handleClick} 
            />
        
            <Html>
                <PopupModal 
                    show={showModal} 
                    onHide={handleModalClose} 
                    title={hotspot.name}
                    size="md"
                >
                    <ListGroup variant="flush">
                        {
                            hotspot.iframeDetails?.map((data, idx) => (
                                <ListGroup.Item key={idx}>
                                    <div className="d-flex justify-content-between align-items-center">
                                        <span className="text-break pe-3">{ data.title }</span>
                                        <Button variant="outline-primary"
                                            onClick={() => viewItem(data)}
                                        >View</Button>
                                    </div>
                                </ListGroup.Item>
                            ))
                        }
                    </ListGroup>
                </PopupModal>

                {showItemModal && item && <PopupModal 
                    show={showItemModal} 
                    onHide={handleItemModalClose} 
                    title={item.title}
                    size="lg"
                >
                    <div className={styles.iframeWrapper}>
                        <iframe
                            src={item.url} 
                            title={item.title}
                            onLoad={handleIframeLoaded}
                        />
                    </div>
                </PopupModal>}
            </Html>
        </>
    )
}

export default IframeHotspot;