import styles from './OffCanvasModal.module.css';
import { MdClose } from "react-icons/md";
import ScrollContent from '../scroll-content/ScrollContent';

const OffCanvasModal = ({ show, onClose, title, children, footer }) => {
    
    if (!show) return null;
    
    return (
        <>
            <div className={styles.offCanvasModalContainer}>
                <div className={styles.modalHeader}>
                    <div className={styles.modalTitle}>
                        <h5>{ title }</h5>
                    </div>
                    <div className={styles.modalCloseBtn} onClick={onClose}>
                        <MdClose />
                    </div>
                </div>
                
                <ScrollContent className={styles.modalBody}>
                    {/* { new Array(1000).fill('fsdfsd dad').join(' ') } */}
                    { children }
                </ScrollContent>
                
                <div className={styles.modalFooter}>
                    { footer }
                </div>
            </div>
            <div className={styles.offCanvasModalBackdrop} onClick={onClose}></div>
        </>
    )
}

export default OffCanvasModal;