import styles from "./EventConfiguration.module.css";
import { MdSettings } from "react-icons/md";
import Button from 'react-bootstrap/Button';
import EventConfigurationModal from "./EventConfigurationModal";
import { useState } from "react";

const EventConfigurationButton = () => {
    
    const [show, setShow] = useState(false);
    
    const hideModal = () => setShow(false);
    
    return (
        <>
            <Button 
                variant="dark"
                className={styles.EventConfigBtn}
                onClick={() => setShow(true)}
            >
                <MdSettings />
            </Button>
            
            { show && <EventConfigurationModal show={show} handleClose={hideModal} /> }
        </>
    )
    
}

export default EventConfigurationButton;