import { useProgress, useTexture } from "@react-three/drei";
import { Suspense } from "react";
import { MEDIA_BASE_URL } from "../../constants/app.constant";
import { panosSelector } from "../../hooks/store/getters";
import { useAppStore } from "../../hooks/store/useAppStore";

const PreloadPanos = () => {

    const panos = useAppStore(panosSelector)

    const panoImageUrls = panos.map(p => MEDIA_BASE_URL + p.image)

    const textures = useTexture(panoImageUrls)

    const Loader = () => {
        const { active, progress, errors, item, loaded, total } = useProgress()

        console.log('loader useProgress ', progress, total);
        return <div>{progress} % loaded</div>
    }


    return (
        <Suspense fallback={Loader}>
            { textures.map(t => null) }
        </Suspense>
    )
}

export default PreloadPanos;