import { HOTSPOT_TYPES } from './../../constants/hotspot.constant';
import MoveHotspotForm from './MoveHotspotForm';
import IframeHotspotForm from './IframeHotspotForm';
import URLHotspotForm from './URLHotspotForm';
import PatchVideo3DHotspotForm from './PatchVideo3DHotspotForm';

const HotspotDetailsForm = ({ hotspotForm, handleInputChange, setHotspotForm }) => {
    
    switch (hotspotForm.type) {
        case HOTSPOT_TYPES.MOVE:
            return <MoveHotspotForm hotspot={hotspotForm} 
                handleInputChange={handleInputChange} 
            />
    
        case HOTSPOT_TYPES.IFRAME:
            return <IframeHotspotForm hotspotForm={hotspotForm} 
                setHotspotForm={setHotspotForm} 
            />
    
        case HOTSPOT_TYPES.URL:
            return <URLHotspotForm hotspotForm={hotspotForm} 
                setHotspotForm={setHotspotForm} 
            />
    
        case HOTSPOT_TYPES.PATCHVIDEO3D:
            return <PatchVideo3DHotspotForm hotspotForm={hotspotForm} 
                setHotspotForm={setHotspotForm} 
            />
    
        default:
            return null;
    }
}

export default HotspotDetailsForm;