import DefaultHotspot from './DefaultHotspot';
import { useState } from 'react';
import { Html } from "@react-three/drei";
import PopupModal from '../popup-modal/PopupModal';
import ListGroup from 'react-bootstrap/ListGroup';
import { Button } from 'react-bootstrap';

const UrlHotspot = ({ hotspot, index, viewMode }) => {
    
    const [showModal, setShowModal] = useState(false);
    
    const handleModalClose = () => {
        setShowModal(false);
    }
    
    const handleClick = () => {
        setShowModal(true);
    }
    
    return (
        <>
            <DefaultHotspot
                hotspot={hotspot} 
                viewMode={viewMode} 
                index={index} 
                onClick={handleClick} 
            />
        
            <Html>
                <PopupModal 
                    show={showModal} 
                    onHide={handleModalClose} 
                    title={hotspot.name}
                    size="md"
                >
                    <ListGroup variant="flush">
                        {
                            hotspot.urlDetails?.map((data, idx) => (
                                <ListGroup.Item key={idx}>
                                    <div className="d-flex justify-content-between align-items-center">
                                        <span className="text-break pe-3">{ data.title }</span>
                                        <Button variant="outline-primary">View</Button>
                                    </div>
                                </ListGroup.Item>
                            ))
                        }
                    </ListGroup>
                </PopupModal>
            </Html>
        </>
    )
}

export default UrlHotspot;