import styles from './HotspotConfigModal.module.css';
import { useAppStore } from './../../hooks/store/useAppStore';
import { getCurrentHotspotSelector, showHotspotConfigModalSelector } from './../../hooks/store/getters';
import { deleteCurrentHotspotSelector, setCurrentHotspotSelector, setShowHotspotConfigModalSelector, updateCurrentHotspotSelector } from './../../hooks/store/setters';
import OffCanvasModal from './../off-canvas-modal/OffCanvasModal';
import Input from '../input/Input';
import { Card, Col, Row } from 'react-bootstrap';
import AppButton from './../button/AppButton';
import { HOTSPOT_TYPES } from '../../constants/hotspot.constant'
import { useEffect, useState } from 'react';
import HotspotDetailsForm from './HotspotDetailsForm';

const hotspotTypeOptions = Object.values(HOTSPOT_TYPES).map(v => ({ value: v, label: v }))

const HotspotConfigModal = (props) => {
    
    const showHotspotConfigModal = useAppStore(showHotspotConfigModalSelector);
    const setShowHotspotConfigModal = useAppStore(setShowHotspotConfigModalSelector);
    const currentHotspot = useAppStore(getCurrentHotspotSelector);
    const setCurrentHotspot = useAppStore(setCurrentHotspotSelector);
    const updateCurrentHotspot = useAppStore(updateCurrentHotspotSelector);
    const deleteCurrentHotspot = useAppStore(deleteCurrentHotspotSelector);
    
    const { hotspot, index } = currentHotspot;

    const [hotspotForm, setHotspotForm] = useState({ ...hotspot });

    useEffect(() => {
        if (currentHotspot && currentHotspot.hotspot) {
            setHotspotForm(currentHotspot.hotspot)
        }
    }, [currentHotspot])

    const handleInputChange = ({ target: { name, value } }) => {
        setHotspotForm({
            ...hotspotForm,
            [name]: value
        })
    }

    const handleFormSubmit = () => {
        console.log('hotspotForm ', {hotspotForm, index});
        updateCurrentHotspot(hotspotForm, index);
    }

    const deleteHotspot = () => {
        console.log('delete hotspot ', {hotspotForm, index});
        deleteCurrentHotspot(index);
        handleModalClose();
        
    }
    
    const handlePositionChange = ({ target: { name, value } }) => {
        setHotspotForm({
            ...hotspotForm,
            position: {
                ...hotspotForm.position,
                [name]: parseFloat(value)
            }
        })
    }


    const handleModalClose = () => {
        setShowHotspotConfigModal(false)
        setCurrentHotspot({})
    }
    
    const ModalFooter = () => {
        return (
            <Row>
                <Col md={4}>
                    <AppButton
                        label="Save"
                        fullWidth
                        onClick={handleFormSubmit}
                    />
                </Col>
                <Col md={4}>
                    <AppButton
                        variant="outline-danger"
                        label="Delete"
                        fullWidth
                        onClick={deleteHotspot}
                    />
                </Col>
                <Col md={4}>
                    <AppButton variant="outline-secondary" 
                        label="Close"
                        fullWidth
                        onClick={handleModalClose}
                    />
                </Col>
            </Row>
        )
    }
    
    if (!showHotspotConfigModal || !currentHotspot) return null;
    
    return (
        <OffCanvasModal title="Hotspot Details"
            show={showHotspotConfigModal}
            onClose={handleModalClose}
            footer={<ModalFooter />}
        >
            <div className={styles.hotspotConfigForm}>
                {/* <pre>{ JSON.stringify(currentHotspot, null, 4) }</pre> */}
                <Input 
                    label="Hotspot Name" 
                    type="text"
                    name="name"
                    value={hotspotForm.name}
                    onChange={handleInputChange}
                />
                <Input 
                    label="Hotspot Icon" 
                    type="text"
                    name="hotspotIcon"
                    value={hotspotForm.hotspotIcon}
                    onChange={handleInputChange}
                />
                <Input 
                    label="Hotspot Type" 
                    type="select"
                    name="type"
                    options={hotspotTypeOptions}
                    value={hotspotForm.type}
                    onChange={handleInputChange}
                />
                <div className="pb-1"></div>
                
                <Card className="my-2">
                    <Card.Header className="text-uppercase">Position</Card.Header>
                    <Card.Body>
                        {/* <Card.Subtitle>Position</Card.Subtitle> */}
                        <Row>
                            <Col md={4}>
                                <Input 
                                    label="X-axis" 
                                    type="number"
                                    name="x"
                                    value={parseFloat(hotspotForm?.position?.x).toFixed(2)}
                                    onChange={handlePositionChange}
                                />
                            </Col>
                            <Col md={4}>
                                <Input 
                                    label="Y-axis" 
                                    type="number"
                                    name="y"
                                    value={parseFloat(hotspotForm?.position?.y).toFixed(2)}
                                    onChange={handlePositionChange}
                                />
                            </Col>
                            <Col md={4}>
                                <Input 
                                    label="Z-axis" 
                                    type="number"
                                    name="z"
                                    value={parseFloat(hotspotForm?.position?.z).toFixed(2)}
                                    onChange={handlePositionChange}
                                />
                            </Col>
                        </Row>
                    </Card.Body>
                </Card>
                
                <HotspotDetailsForm 
                    hotspotForm={hotspotForm} 
                    setHotspotForm={setHotspotForm} 
                    handleInputChange={handleInputChange}
                />
            </div>
        </OffCanvasModal>
    )
}

export default HotspotConfigModal;