import Hotspot from './../../components/hotspots/Hotspot';

const PanoHotspots = ({ hotspots, viewMode=false }) => {
    
    if (!hotspots) return null;
  
    return (
        <>
            { 
                hotspots.map((hp, index) => <Hotspot key={index} 
                    index={index} hotspot={hp} viewMode={viewMode} />
                )
            }
        </>
    )
};


export default PanoHotspots;