import styles from './ManageEvent.module.css';
import Panorama from './../panorama/Panorama';
import Header from './../../components/header/Header';
import AddPanoramaButton from './../../components/add-panorama/AddPanoramaButton';
import EditPanoramaButton from '../../components/edit-panorama/EditPanoramaButton';
import EventConfigurationButton from './../../components/event-configuration/EventConfigurationButton';
import HotspotConfigModal from './../../components/hotspot-config-modal/HotspotConfigModal';
import WelcomeVideo from '../../components/welcome-video/WelcomeVideo';

const ManageEvent = () => {
    
    return (
        <>
            <Header />
            <Panorama viewMode={false} />
            <HotspotConfigModal />
            
            <div className={styles.fabContainer}>
                <AddPanoramaButton />
                <EditPanoramaButton />
                <EventConfigurationButton />
            </div>

            <WelcomeVideo />
        </>
    )
}

export default ManageEvent;