import { panosSelector } from '../../hooks/store/getters';
import { setCurrentPanoSelector } from '../../hooks/store/setters';
import { useAppStore } from '../../hooks/store/useAppStore';
import DefaultHotspot from './DefaultHotspot';

const MoveHotspot = ({ hotspot, index, viewMode }) => {

    const setCurrentPano = useAppStore(setCurrentPanoSelector);
    const panos = useAppStore(panosSelector);
    
    
    const handleClick = () => {
        // eslint-disable-next-line
        const goToPano = panos.findIndex(p => p.id == hotspot.goToPano)
        console.log('move hotspot goto pano', goToPano);
        if (goToPano > -1) {
            setCurrentPano(goToPano)
        }
    }
    
    return (
        <DefaultHotspot hotspot={hotspot} viewMode={viewMode} index={index} onClick={handleClick} />
    )
}

export default MoveHotspot;