import { Button } from 'react-bootstrap';
import styles from './WelcomeVideo.module.css';

const WelcomeVideo = ({ onVideoEnded }) => {

    const onDone = () => {
        console.log('video ended');
        onVideoEnded();
    }

    const onSkip = () => {
        console.log('video skipped');
        onVideoEnded();
    }

    const onStart = () => {
        console.log('video start');
    }

    return (
        <div className={styles.welcomeVideoWrapper}>
            <video 
                className={styles.welcomeVideo}
                src="https://www.roshn.sa/-/media/project/roshn/banner/banner-videos/roshn-mv-final-ar_01.mp4"
                autoPlay={true}
                playsInline={true}
                controls={false}
                muted={false}
                onEnded={onDone}
                onPlay={onStart}
            ></video>

            <Button
                variant="outline-light"
                className={styles.skipBtn}
                onClick={onSkip}
            >Skip</Button>
        </div>
    )
}

export default WelcomeVideo;